body{
  background-color: rgb(180, 109, 109);
}

.App{
  background-color: rgb(117, 58, 117);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  background-image: url(data:image/jpeg;base64,);
  
}

#to-do-form input{
  background-color: rgb(63, 124, 114);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin:20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
  
  
}



#to-do-form-input::placeholder{
  color: rgba(255,255,255,0.5);
}

#to-do-form-input :focus,#to-do-form button:focus{
  outline: none;
}

#to-do-form button{
  height:50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(202, 156, 24);
  cursor: pointer;
  font-weight: bolder;
  color: white;

  
}

img{
  height:200px ;
  width:200px;
 margin: 20px ;
 margin-left: 75px;
}
